<template>
    <div class="documents">
        <DocumentGrid v-if="mode === 'grid'" :documents="filteredDocuments"
            @update-doc="$emit('update-doc', $event)"
            @delete-doc="$emit('delete-doc', $event)"/>
        <DocumentList v-if="mode === 'list'" :documents="filteredDocuments" 
            @update-doc="$emit('update-doc', $event)"
            @delete-doc="$emit('delete-doc', $event)"/>
    </div>
</template>

<script>
import { defineComponent } from 'vue';
import DocumentGrid from '@/components/documents/DocumentGrid.vue';
import DocumentList from '@/components/documents/DocumentList.vue';


export default defineComponent({
    name: "IntrigueDocuments",
    components: {
        DocumentGrid,
        DocumentList
    },

    props: {
        documents: Array,
        mode: String,
        search: String,
    },

    data() {
        return { };
    },

    methods: {},

    computed: {
        filteredDocuments() {
            return this.documents;
        }
    },

    watch: {},
});
</script>

