<template>
    <va-list>
        <va-list-item v-for="doc in documents" :key="doc.id"
            :to="{
                name: 'document', 
                params: { key: doc.key, title: doc.name }
            }">
            <va-list-item-section avatar>
                <va-avatar>
                    <Avatar class="avatar" :name="doc.key" />
                </va-avatar>
            </va-list-item-section>

            <va-list-item-section>
                <va-list-item-label>
                    <b  style="margin-right: 0.5rem;"
                        :contenteditable="editingDocName === doc.key"
                        :ref="`title${doc.key}`"
                        @blur="toggleEditTitle(doc.id, doc.key)"
                        @click="onTitleClicked(doc.key, $event)">
                        {{ doc.name }}
                    </b>
                    <va-button flat size="small" icon="edit"
                        :disabled="editingDocName === doc.key"
                        @click.prevent="toggleEditTitle(doc.id, doc.key)"/>
                </va-list-item-label>

                <va-list-item-label caption>
                    Last updated on {{ doc.dateUpdated.toLocaleString() }}
                </va-list-item-label>
            </va-list-item-section>

            <va-list-item-section icon>
                <!-- <va-button flat @click.prevent>
                    <va-icon name="content_copy" size="small"/>
                    Duplicate
                </va-button> -->
                <va-button flat @click.prevent="$emit('delete-doc', doc.id)">
                    <va-icon name="delete" size="small"/>
                    Delete
                </va-button>
            </va-list-item-section>
        </va-list-item>
    </va-list>
</template>

<script>
import { defineComponent } from 'vue';
import {
    VaButton,
    VaList,
    VaListItem,
    VaListItemLabel,
    VaListItemSection,
    VaAvatar,
    VaIcon
} from 'vuestic-ui';
import Avatar from "vue-boring-avatars";

// import { nanoid } from 'nanoid'

export default defineComponent({
    name: "IntrigueDocumentList",
    components: {
        VaButton,
        VaList,
        VaListItem,
        VaListItemLabel,
        VaListItemSection,
        VaAvatar,
        VaIcon,
        Avatar
    },

    props: {
        documents: Array,
    },

    data() {
        return {
            editingDocName: null
        };
    },

    methods: {
        onTitleClicked(key, event) {
            if (this.editingDocName === key) event.preventDefault();
        },


        toggleEditTitle(id, key) {
            const title = this.$refs[`title${key}`][0];
            console.log(title);

            if (this.editingDocName === key) {
                this.$emit('update-doc', {
                    id: id, 
                    name: title.textContent
                })
                this.editingDocName = null;
            } else {
                this.editingDocName = key;
                setTimeout(() => {
                    title.focus();
                    let sel, range;
                    if (window.getSelection && document.createRange) {
                        range = document.createRange();
                        range.selectNodeContents(title);
                        sel = window.getSelection();
                        sel.removeAllRanges();
                        sel.addRange(range);
                    } else if (document.body.createTextRange) {
                        range = document.body.createTextRange();
                        range.moveToElementText(title);
                        range.select();
                    }
                }, 0)
            }
        },
    }
});
</script>

