    <div class="home" :style="{ background: isDark ? 'var(--va-dark)' : 'var(--va-background)' }">
        <va-navbar class="nav" :color="isDark ? 'dark' : 'background'">
            <template #left>
                <va-navbar-item>
                    <va-button flat :icon="minimizeSidebar ? 'menu' : 'menu_open'"
                        @click="minimizeSidebar = !minimizeSidebar" />
                </va-navbar-item>
            </template>
            <template #center> <b>Intrigue</b> </template>
            <template #right>
                <va-navbar-item>
                    <va-popover message="Shauna Zhang" color="gray">
                        <va-avatar :size="37">
                            <Avatar class="avatar" variant="beam" name="Shauna Zhang" />
                        </va-avatar>
                    </va-popover>
                </va-navbar-item>
            </template>
        </va-navbar>

        <div class="content" style="height: 90%; display: flex; flex-flow: row nowrap; gap: 1rem;">
            <va-sidebar textColor="dark" style="flex: 0 0;"
                :style="{
                    'flex-basis': minimizeSidebar ? 'auto' : '15rem'
                }" 
                :minimized="minimizeSidebar" minimizedWidth="64px">
                <va-sidebar-item 
                    :active="sidebar === 'documents'" 
