                        </va-sidebar-item-title>
                    </va-sidebar-item-content>
                </va-sidebar-item>

                <va-sidebar-item 
                    :active="sidebar === 'trash'" 
                    @click="sidebar = 'trash'">

                    <va-sidebar-item-content>
                        <va-icon name="delete" />
                        <va-sidebar-item-title v-if="!minimizeSidebar">
                            Trash
                        </va-sidebar-item-title>
                    </va-sidebar-item-content>
                </va-sidebar-item>
            </va-sidebar>

            <div style="flex-grow: 1; display: flex; flex-flow: column nowrap;">

                <va-navbar color="background">
                    <template #left>
                        <va-button icon="add_circle" size="medium" 
                            v-if="sidebar === 'documents'"
