                            :placeholder="`Search within ${sidebar}`">
                            <template #prependInner>
                                <va-icon name="search" />
                            </template>
                        </va-input>

                    </template>
                    <template #center>
                    </template>
                    <template #right>

                        <va-button-group size="medium" outline>
                            <va-button icon="grid_view" 
                                :disabled="mode === 'grid'" @click="mode = 'grid'"/>
                            <va-button icon="format_list_bulleted" 
                                :disabled="mode === 'list'" @click="mode = 'list'"/>
                        </va-button-group>
                    </template>
                </va-navbar>

                <Documents :documents="sidebar === 'documents' ? documents : trash"
                    :mode="mode" :search="search"
                    @update-doc="updateDocument($event)"
                    @delete-doc="trashDocument($event)"/>

            </div>
        </div>
        <!-- <settings :show="true" /> -->
    </div>
</template>

<script>
import { defineComponent } from 'vue';
import {
    VaNavbar,
    VaNavbarItem,
    VaAvatar,
    VaIcon,
    VaButton,
    VaPopover,
    VaInput, 
    VaButtonGroup,
    VaSidebar, VaSidebarItem, VaSidebarItemTitle, VaSidebarItemContent
} from 'vuestic-ui';
import Avatar from "vue-boring-avatars";

import Documents from '@/components/documents/Documents.vue';
// import Settings from '@/components/settings/Settings.vue';
import { Database } from '@/store/database';

export default defineComponent({
    name: 'HomeView',
    components: {
        Avatar,
        VaNavbar,
        VaNavbarItem,
        VaAvatar,
        VaIcon,
        VaButton,
        VaPopover,
        VaInput,
        VaButtonGroup,
        VaSidebar,
        VaSidebarItem,
        VaSidebarItemTitle,
        VaSidebarItemContent,
        Documents,
        // Settings
    },

    data() {
        return {
            isDark: false,
            minimizeSidebar: false,
            search: '',
            sidebar: 'documents',
            mode: 'list',
            documents: [],
            trash: [],
        }
    },

    created () {
        this.database = new Database();
        this.refreshDatabase();
    },

    methods: {
        async createDocument() {
            await this.database.createDocument();
            await this.refreshDatabase();
        },

        async updateDocument(event) {
            await this.database.updateDocument(event.id, event);
            await this.refreshDatabase();
        },

        async trashDocument(id) {
            await this.database.trashDocument(id);
            await this.refreshDatabase();
        },

        async recoverDocument() {

        },

        async deleteDocument() {

        },

        async refreshDatabase() {
            this.documents = await this.database.getDocuments();
            this.trash = await this.database.getTrash();
        }
    }
});
</script>

